@tailwind base;
@tailwind components;
@tailwind utilities;


@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

:root {
    --primary-dark: #381aa1;
    --primary-light: rgb(94, 162, 240);
    --index: calc(1vw + 1vh);
	--transition: 1.5s cubic-bezier(.05, .5, 0, 1);
   
}

@layer base{
    html {
        @apply scroll-smooth
    }

    body{
        @apply font-[Poppins] bg-[#191331]  text-white
    }

    li {
        @apply p-4 text-sm md:text-lg
    }
    button {
        @apply bg-purple-600 text-white py-2 px-4 rounded-full text-center text-base shadow-md shadow-white transition duration-1000 ease-in-out hover:bg-white hover:text-black hover:shadow-md hover:shadow-purple-700 hover:scale-105
    }

    h2{
        @apply text-xl font-bold
    }
}
@layer utilities {
    .boxshadow {
      box-shadow: 0 0 5px rgb(147 51 234 / var(--tw-bg-opacity)),
                  0 0 5px rgb(147 51 234 / var(--tw-bg-opacity)),
                  0 0 5px rgb(147 51 234 / var(--tw-bg-opacity)),
                  0 0 5px rgb(147 51 234 / var(--tw-bg-opacity));
    }
  }